// The initial language
let gCurrLang = 'en';

// Change the text
export const setLangService = (lang) => {
  gCurrLang = lang;

  _bodyClass(lang);
  _doTrans(lang);
};

// Bringing a translation
function getTransText(el, lang) {
  let elTrans = el.dataset.trans;
  return getTrans(elTrans, lang);
}

function getTrans(transKey, lang) {
  let keyTrans = gTrans[transKey];
  if (!keyTrans) return 'UNKNOWN';
  let txt = keyTrans[lang];
  if (!txt) txt = keyTrans.en;
  return txt;
}

const _bodyClass = (lang) => {
  let elBody = document.querySelector('body');
  if (lang === 'he') {
    elBody.classList.add('rtl');
  } else {
    elBody.classList.remove('rtl');
  }
};

const _doTrans = (lang) => {
  let els = document.querySelectorAll('[data-trans]');
  els.forEach((el) => {
    if (el.nodeName === 'INPUT') {
      el.placeholder = getTransText(el, lang);
    } else {
      el.innerText = getTransText(el, lang);
    }
  });
};

function formatNumOlder(num) {
  return num.toLocaleString('es');
}

function formatNum(num) {
  return new Intl.NumberFormat(gCurrLang).format(num);
}

function formatCurrency(num) {
  return new Intl.NumberFormat('he-IL', { style: 'currency', currency: 'ILS' }).format(num);
}

function formatDate(time) {
  let options = {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: 'numeric',
    minute: 'numeric',
    hour12: true,
  };

  return new Intl.DateTimeFormat(gCurrLang, options).format(time);
}
