import React, { useState, useEffect } from 'react';
import { SongPreview } from './SongPreview';

import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import AccessTimeIcon from '@mui/icons-material/AccessTime';
import { Loading } from '../global/Loading';

export const SongList = ({
  songs,
  playlistName,
  selectSong,
  toggleFavorite,
  favoriteSong,
  updatePlaylistSongs = () => {},
  isEdit = false,
  editSongs = [],
}) => {
  const [songsToShow, setSongsToShow] = useState(songs);

  useEffect(() => {
    if (songsToShow !== songs) {
      updatePlaylistSongs(songsToShow);
    }
  }, [songsToShow]);

  useEffect(() => {
    setSongsToShow(songs);
  }, [songs]);

  const handleOnDragEnd = (result) => {
    if (!result.destination) return;
    const items = Array.from(songsToShow);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setSongsToShow(items);
  };

  if (!songs) return <Loading />;
  return (
    <>
      <ul className="song-list">
        <div className="list-title">
          {playlistName ? (
            <div className="title-container">
              <AccessTimeIcon className="clock" />
              {playlistName ? <p data-trans="date-added">Date added</p> : <p></p>}
              {playlistName ? <p data-trans="album">album</p> : <p></p>}
              <p data-trans="title">title #</p>
            </div>
          ) : null}
          <span></span>
        </div>
        <DragDropContext onDragEnd={handleOnDragEnd}>
          <Droppable droppableId="characters">
            {(provided) => (
              <ul className="characters clean-list" {...provided.droppableProps} ref={provided.innerRef}>
                {songsToShow.map((song, idx) => {
                  return (
                    <Draggable key={song._id} draggableId={song._id} index={idx}>
                      {(provided) => (
                        <li ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                          <SongPreview
                            song={song}
                            songNum={idx + 1}
                            playlistName={playlistName}
                            selectSong={selectSong}
                            toggleFavorite={toggleFavorite}
                            favoriteSong={favoriteSong}
                            isEdit={isEdit}
                            editSongs={editSongs}
                          ></SongPreview>
                        </li>
                      )}
                    </Draggable>
                  );
                })}
                {provided.placeholder}
              </ul>
            )}
          </Droppable>
        </DragDropContext>
      </ul>
    </>
  );
};
