const INITIAL_STATE = {
  currUser: {},
  userMsg: null,
};

export function userReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case 'SET_LOGIN_USER':
      return {
        ...state,
        currUser: { ...action.currUser },
      };
    case 'USERMSG':
      return {
        ...state,
        userMsg: action.msg,
      };

    default:
      return state;
  }
}
