import React from 'react';
import { Link, NavLink } from 'react-router-dom';

export const MenuMobile = ({ toggleMenu, onSetLang }) => {
  return (
    <section className="menu">
      <a className="close" onClick={toggleMenu}>
        X
      </a>

      <div className="menu-container">
        <div className="login-container">
          <NavLink to="/login" data-trans="login" onClick={toggleMenu}>
            Login
          </NavLink>
          <NavLink to="/login/signUp" data-trans="sign-up" onClick={toggleMenu}>
            SignUp
          </NavLink>
          --
          <NavLink to="/genre/Your Playlist">
            <span className="txt" data-trans="your-library" onClick={toggleMenu}>
              Your library
            </span>
          </NavLink>
          <NavLink to="/editItem">
            <span data-trans="create-a-playlist" className={`txt `} onClick={toggleMenu}>
              Create a playlist
            </span>
          </NavLink>
        </div>
        <select id="inputState" className="form-control-xsm" onChange={(ev) => onSetLang(ev)}>
          <option value="en">English</option>
          <option value="he"></option>
          <option value="es">Spanish</option>
        </select>
      </div>
    </section>
  );
};
