import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { PlaylistPreview } from './PlaylistPreview';

import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { useDispatch } from 'react-redux';

export const Playlists = ({ playlists, header, page }) => {
  let history = useHistory();

  const dispatch = useDispatch();

  const [playlistsToShow, setPlaylistsToShow] = useState(playlists);

  useEffect(() => {
    if (playlistsToShow !== playlists) {
      // dispatch()
    }
  }, [playlistsToShow]);

  useEffect(() => {
    setPlaylistsToShow(playlists);
  }, [playlists]);

  const openPlaylist = (playlistId) => {
    history.push(`/item/${playlistId}`);
  };

  const handleOnDragEnd = (result) => {
    if (!result.destination) return;
    const items = Array.from(playlistsToShow);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);

    setPlaylistsToShow(items);
  };

  return (
    <div className={`playlist ${page}`}>
      <div className="header">
        <h2>
          <a data-trans={header} onClick={() => history.push(`/genre/${header}`)} className="header-title-web">
            {header}
          </a>
          <a data-trans={header} className="header-title-mob">
            {header}
          </a>
        </h2>
        <p data-trans="see-all" onClick={() => history.push(`/genre/${header}`)}>
          see all
        </p>
      </div>
      <DragDropContext onDragEnd={handleOnDragEnd}>
        <Droppable droppableId="characters">
          {(provided) => (
            <ul className="characters clean-list" {...provided.droppableProps} ref={provided.innerRef}>
              {playlists &&
                playlists.length &&
                playlistsToShow.map((playlist, idx) => (
                  <Draggable key={playlist._id} draggableId={playlist._id} index={idx}>
                    {(provided) => (
                      <li ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                        <PlaylistPreview
                          playlist={playlist}
                          key={playlist._id ? playlist._id : playlist.pId}
                          openPlaylist={openPlaylist}
                        />
                      </li>
                    )}
                  </Draggable>
                ))}
              {provided.placeholder}
            </ul>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
};
